.main-header {
  position: relative;
  z-index: 1;
  background-color: $color-deep-sky-blue;
  background-image: image-set("../img/background/background-header-desktop@2x.webp" 2x, "../img/background/background-header-desktop.webp" 1x);
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

  @include vp-1023 {
    background-image: image-set("../img/background/background-header-tablet@2x.webp" 2x, "../img/background/background-header-tablet.webp" 1x);
    box-shadow: none;
  }

  @include vp-767 {
    background-image: image-set("../img/background/background-header-mobile@2x.webp" 2x, "../img/background/background-header-mobile.webp" 1x);
  }
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 35px;
  padding-right: 40px;
  padding-left: 50px;

  @include vp-1023 {
    padding-top: 29px;
    padding-right: 35px;
    padding-left: 45px;
  }

  @include vp-767 {
    padding-top: 22px;
    padding-right: 20px;
    padding-left: 17px;
  }
}

.main-header__logo {
  & svg {
    fill: $color-default-white;

    @include vp-1023 {
      width: 38px;
      height: 20px;
    }
  }
}

.main-header__toogle {
  display: none;
  width: 24px;
  height: 12px;
  cursor: pointer;
  border: none;
  background-color: inherit;

  @include vp-767 {
    display: block;
    align-self: flex-end;
  }
}

.main-header__overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 43, 102, 0.5);
  display: none;
}

.main-header--opened .main-header__overlay {
  @include vp-767 {
    display: block;
    z-index: 0;
  }
}

.main-header--opened .main-header__toogle {
  & svg {
    display: none;
  }

  @include vp-767 {
    position: absolute;
    top: 0;
    right: 0;
    background-image: url("../img/svg/menu-close.svg");
    background-repeat: no-repeat;
    background-position: center center;
    margin-right: 10px;
    margin-top: 25px;
    z-index: 2;
  }
}

.nojs .main-header__toogle {
  @include vp-767 {
    display: none;
  }
}

.main-header--opened .main-header__logo {
  @include vp-767 {
    position: absolute;
    z-index: 13;
    top: 20px;
    left: 5%;

    & svg {
      fill: $color-default-black;
    }
  }
}

.main-header__navigation {
  padding-top: 8px;
  max-width: 700px;

  @include vp-1023 {
    padding-top: 0;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

.main-header--opened .main-header__wrapper {
  @include vp-767 {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 89%;
    height: 101vh;
    overflow-y: hidden;
    z-index: 11;
    background-color: $color-default-white;
  }
}

.main-header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 14px;

  @include vp-1023 {
    gap: 4px;
  }

  @include vp-767 {
    display: none;
  }
}

.main-header--opened .main-header__list {
  @include vp-767 {
    display: flex;
    padding-top: 64px;
    gap: 25px;
  }
}

/* .main-header--opened li {
  @include vp-767 {
    display: block;
    margin: 0 0 26px 0;
  }
} */

.main-header__list a {
  position: relative;
  padding: 10px;
  font-weight: 500;
  line-height: 20px;
  color: $color-default-white;

  &:hover {
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 4px;
      background-color: $color-cerulean;
    }
  }

  &:focus-visible {
    outline: 4px solid $color-cerulean;
  }

  &:active {
    opacity: 0.5;
  }

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 22px;
  }
}

.main-header--closed .main-header__list {
  @include vp-767 {
    display: none;
  }
}

.nojs .main-header__wrapper {
  @include vp-767 {
    position: inherit;
    align-items: flex-start;
    background-color: $color-alice-blue;
  }
}

.nojs .main-header__list {
  @include vp-767 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 26px;
    margin-bottom: 30px;
  }
}

.main-header--opened a,
.nojs a {
  @include vp-767 {
    color: $color-default-black;

    &:hover {
      color: $color-cerulean;
    }

    &:focus-visible {
      outline: 4px solid $color-cerulean;
    }

    &:active {
      opacity: 0.5;
    }
  }
}

.nojs .main-header__logo {
  @include vp-767 {
    & svg {
      fill: $color-default-black;
    }
  }
}

.main-header__intro {
  padding-top: 190px;
  padding-left: 145px;
  padding-bottom: 280px;
  color: $color-default-white;

  @include vp-1023 {
    padding-top: 212px;
    padding-left: 45px;
    padding-bottom: 275px;
  }

  @include vp-767 {
    padding-top: 127px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 177px;
  }
}

.main-header--opened .main-header__intro {
  margin-top: 175px;
}

.main-header__intro h1 {
  margin: 0;
  padding: 0;
  margin-bottom: 12px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;

  @include vp-1023 {
    font-size: 50px;
    line-height: 61px;
  }

  @include vp-767 {
    font-size: 36px;
    line-height: 125%;
  }
}

.main-header__intro p {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 24px;

  @include vp-1023 {
    font-size: 18px;
    line-height: 22px;
  }

  @include vp-767 {
    padding-right: 75px;
    font-size: 16px;
    line-height: 140%;
  }
}
